html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 28px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #2b475b;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.btn-reset {
  padding: 0;
  border: none;
  background-color: rgba(255, 255, 255, 0);
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 130px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 130px;
  }
}
@media (max-width: 1279px) {
  .container {
    padding: 0 50px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 35px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 139px;
  }
}
@media (max-width: 479px) {
  .container {
    padding: 0 20px;
  }
}

.header {
  padding: 63px 0;
  background-color: #133249;
  transition: background-color 0.3s ease;
}
@media (max-width: 1023px) {
  .header {
    padding: 50px 0;
  }
}
@media (max-width: 767px) {
  .header {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 3;
    width: 100%;
    padding: 30px 0;
  }
}
.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #ffffff;
}
@media (max-width: 767px) {
  .header__wrapper {
    position: relative;
    flex-direction: column;
    justify-content: initial;
    align-items: initial;
  }
}
.header__panel {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header .logo {
  flex-shrink: 0;
  margin-right: 35px;
}
.header__button {
  flex-shrink: 0;
  margin-left: 35px;
}
@media (max-width: 767px) {
  .header__button {
    margin-left: 0;
    margin-bottom: 50px;
  }
}

@media (max-width: 767px) {
  header.is-open-menu {
    bottom: 0;
    overflow-y: auto;
    background-color: #ffffff;
  }
  header.is-open-menu .header__panel {
    margin-bottom: 38px;
  }
  header.is-open-menu .logo {
    color: #123149;
  }
}
.main-nav {
  display: contents;
}
@media (max-width: 767px) {
  .main-nav {
    position: absolute;
    top: 87px;
    z-index: 3;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    overflow: hidden;
  }
  .main-nav .main-nav__item + .main-nav__item {
    margin-top: 40px;
  }
}
.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  margin-right: -36px;
  padding: 0;
  list-style: none;
}
@media (max-width: 767px) {
  .main-nav__list {
    flex-direction: column;
    margin-bottom: 50px;
  }
}
.main-nav__item {
  margin-right: 36px;
}
.main-nav__link {
  display: block;
  color: #ffffff;
}
.main-nav__link.is-active {
  color: #0ecb78;
}
.main-nav__link[href] {
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .main-nav__link[href]:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.main-nav__link[href].focus-visible:focus {
  opacity: 0.8;
}

@media (max-width: 767px) {
  .is-open-menu .main-nav {
    display: flex;
    transition: height 0.6s ease-out;
  }
  .is-open-menu .main-nav__list {
    margin-right: 0;
  }
  .is-open-menu .main-nav__item {
    margin-right: 0;
  }
  .is-open-menu .main-nav__link {
    color: #123149;
  }
  .is-open-menu .main-nav__link.is-active {
    color: #0ecb78;
  }
}
.footer {
  padding: 85px 0 85px 0;
  color: #a5adba;
  background-color: #133249;
}
@media (max-width: 767px) {
  .footer {
    padding: 50px 0 25px 0;
  }
}
.footer__wrapper {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .footer__wrapper {
    flex-direction: column;
    justify-content: initial;
  }
}
.footer__poplar {
  width: 390px;
}
@media (max-width: 767px) {
  .footer__poplar {
    order: 1;
    width: 100%;
  }
}
.footer .logo {
  margin-bottom: 35px;
}
@media (max-width: 767px) {
  .footer .logo {
    margin-bottom: 30px;
  }
}
.footer__text {
  margin: 0;
  margin-bottom: 30px;
  font-size: 16px;
  font-weight: 500;
}
@media (max-width: 767px) {
  .footer__text {
    font-size: 14px;
    line-height: 20px;
  }
}
.footer__social-list {
  display: flex;
  flex-wrap: wrap;
  gap: 23px;
  align-items: center;
  margin: 0;
  margin-bottom: 35px;
  padding: 0;
  list-style: none;
}
@media (max-width: 767px) {
  .footer__social-list {
    margin-bottom: 30px;
  }
}
.footer__social-link {
  display: block;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .footer__social-link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.footer__social-link.focus-visible:focus {
  opacity: 0.8;
}
.footer__copyright {
  margin: 0;
  font-size: 14px;
  font-weight: 500;
}
.footer__menu {
  display: flex;
  justify-content: space-between;
  width: 375px;
  margin-left: 60px;
}
@media (max-width: 767px) {
  .footer__menu {
    width: 100%;
    margin-bottom: 78px;
    margin-left: 0;
  }
}
.footer__menu-column {
  width: 130px;
}
@media (max-width: 767px) {
  .footer__menu-column {
    width: 115px;
  }
}
.footer .footer__menu-column + .footer__menu-column {
  margin-left: 30px;
}
.footer__menu-column-title {
  margin: 0;
  margin-bottom: 50px;
  color: #ffffff;
  font-size: 20px;
  line-height: 25px;
  font-weight: 600;
}
@media (max-width: 767px) {
  .footer__menu-column-title {
    margin-bottom: 40px;
    font-size: 18px;
    line-height: 22px;
  }
}
.footer__menu-column-link {
  display: block;
  color: #a5adba;
  font-size: 16px;
  line-height: 20px;
  font-weight: 500;
  transition: opacity 0.3s ease;
}
@media (max-width: 767px) {
  .footer__menu-column-link {
    font-size: 14px;
    line-height: 17px;
  }
}
@media (hover: hover) {
  .footer__menu-column-link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.footer__menu-column-link.focus-visible:focus {
  opacity: 0.8;
}
.footer .footer__menu-column-link + .footer__menu-column-link {
  margin-top: 26px;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.btn {
  display: inline-flex;
  justify-content: center;
  padding: 16px 30px;
  border-radius: 60px;
  border: none;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 600;
  font-size: 16px;
  color: #ffffff;
  text-transform: uppercase;
  background-color: #0ecb78;
  cursor: pointer;
  transition: opacity 0.3s ease;
  /*
  @include vp-1023 {
    padding: 11px 25px;

    font-size: 14px;
  }
  */
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

@media (max-width: 767px) {
  .btn:not(header .btn) {
    width: 100%;
    padding: 11px 25px;
    font-size: 14px;
  }
}

.custom-input,
.custom-textarea {
